
<template>
  <div id="app">
    <div class="width:100%;margin-top:10px" v-for="item in items" v-on:click="open(item)">
      <span class="" style="margin-left: 5px">{{item.name}}</span>
    </div>
  </div>
</template>

<script type="text/javascript">
  import API from '../common/API.js';
  export default {
    data(){
      return {
        items:[
          // {name:'1'},{name:'2'},{name:'3'},{name:'4'},{name:'5'},{name:'6'},{name:'7'}
        ]
      }
    },
    methods:{
      open:function (item) {
        this.$router.push({name: `rpcDetail`,query:{
            item:item
          }});
      }
    },
    created:function () {
      var _this = this;
      API.getList(function (data) {
        _this.items = data || [];
      });
    },
    mounted:function () {
    }
  }
</script>



