/* 项目启动 */
import Vue from 'vue'
import App from './App'
import router from './router'
import FastClick from 'fastclick'
import listview from './components/listview.vue'

Vue.use(listview);
FastClick.attach(document.body)
new Vue({
  router: router,
  render: h => h(App)
  // components: { firstcomponent, secondcomponent }
}).$mount('#app')



