
<template>
  <div id="app">
      <span class="" style="margin-left: 5px">{{item.name}}</span>
  </div>
</template>

<script type="text/javascript">
  export default {
    data(){
      return {
        item:{}
      }
    },
    methods:{
    },
    mounted:function () {
      this.item = this.$route.query.item;
    }
  }
</script>



