<template>
  <div>
    <h2 style="text-align: center">这是测试页</h2>
    <button style="margin-top: 10px;margin-left:auto; width: 250px" v-on:click="openDefault">跨应用跳转-打开默认页</button>
    <button style="margin-top: 10px;margin-left:auto;width: 250px" v-on:click="openPage">跨应用跳转-打开指定页</button>
    <button style="margin-top: 10px;margin-left:auto;width: 250px" v-on:click="openPageParam">跨应用跳转-打开指定页并传参</button>
    <button style="margin-top: 10px;margin-left:auto;width: 250px" v-on:click="jumpRpc">rpc</button>
    <button style="margin-top: 10px;margin-left:auto;width: 250px" v-on:click="jump">本应用路由跳转</button>
  </div>
</template>
<script>
  import API from "../common/API";

  export default{
    data () {
      return{
      }
    },
    created () {
    },
    methods: {
      ready :function (callback) {
        // 如果jsbridge已经注入则直接调用
        if (window.AlipayJSBridge) {
          callback && callback();
        } else {
          // 如果没有注入则监听注入的事件
          document.addEventListener('AlipayJSBridgeReady', callback, false);
        }
      },
      openDefault: function() {
        API.openApp('95279527');
      },
      openPage: function() {
        API.openApp('95279527','news');
      },
      openPageParam: function() {
        var param = {
          id:'123',
          data:'这是data'
        };
        API.openApp('95279527','news',param);
      },
      jump:function () {
        this.$router.push({name: `news`,query:{
          params:'aaa'
          }});
      },
      jumpRpc:function () {
        this.$router.push({name: `rpcList`});
      }
    }
  }
</script>

<style>
  button{
    display: block;
    margin: 0 auto;
    line-height: 30px;
    border: 1px solid #ddd;
    color: #41b883;
  }
  a{
    color: #35495e;
    font-size: 16px;
  }
  ul{
    margin-bottom: 60px;
  }
  li{
    line-height: 32px;
    border-bottom: 1px solid #ddd;
    padding: 0 10px;
  }
  b{
    font-size: 12px;
    color: #35495e;
  }
  .loading{
    text-align: center;
  }
</style>



