<template>
  <div>
    <listview
      ref="list"
      page-start-field="current_page"
      page-limit-field="page_row_count"
      result-has-more-field="hasMore"
      result-list-field="list"
      :on-request-data="onRequestData"
      :has-more="true"
    >
      <!-- 这是循环的每一块的模板，独立状态来自于state.item -->
      <div
        slot-scope="state"
        class="item"
      >
        <span>
          {{state.item.name}}
        </span>
      </div>
    </listview>
  </div>

</template>
<style>
  .btn {
    background-image: -webkit-linear-gradient(top,#f8f8f8,#f1f1f1);
    background-color: #f8f8f8;
    border-radius: 2px;
    color: #757575;
    font-size: 13px;
    font-weight: bold;
    margin: 11px 4px;
    min-width: 54px;
    padding: 0 16px;
    text-align: center;
    border: 1px solid #c6c6c6;
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
    color: #222;
  }

  .item {
    height: 50px;
    line-height: 50px;
    text-align: center;
    border-bottom: 1px solid #eee;
  }
</style>
<script>
  // ajax 使用官方推荐的 axios
  import axios from 'axios'
  import Listview from '../components/listview.vue'

  export default{
    components: {
      Listview
    },
    data () {
      return{
        loading: false,
        listArr: []
      }
    },
    created () {
      // this.loadList();
    },
    methods: {
      onRequestData(params, callback) {
        console.log(JSON.stringify(params));
        var _this = this;
        const {
          current_page,
          page_row_count
        } = params;

        axios.get('https://api.github.com/search/code?q=addClass+in:file+language:js+repo:jquery/jquery', {
          params: {}
        })
          .then(function (response) {
            console.log(response.data.items)
            for(var i=0;i<response.data.items.length;i++){
              _this.listArr.push(response.data.items[i]);
            }
            callback({
              list: _this.listArr,
              hasMore: true
            });
          })
      }
    }
  }
</script>

<style>
  button{
    display: block;
    margin: 0 auto;
    line-height: 30px;
    border: 1px solid #ddd;
    color: #41b883;
  }
  a{
    color: #35495e;
    font-size: 16px;
  }
  ul{
    margin-bottom: 60px;
  }
  li{
    line-height: 32px;
    border-bottom: 1px solid #ddd;
    padding: 0 10px;
  }
  b{
    font-size: 12px;
    color: #35495e;
  }
  .loading{
    text-align: center;
  }
</style>



