var isRpc = true;
import axios from 'axios'
export default {
  ready :function (callback) {
    // 如果jsbridge已经注入则直接调用
    if (window.AlipayJSBridge) {
      callback && callback();
    } else {
      // 如果没有注入则监听注入的事件
      document.addEventListener('AlipayJSBridgeReady', callback, false);
    }
  },

  getList:function (callback) {
    var items = [];
    if(isRpc){
      // alert("調用rpc：test_list");
      this.ready(function() {
        AlipayJSBridge.call('rpc', {
          operationType: 'test_list',
          requestData: [],
          headers:{}
        }, function (response) {
          return callback(response.data||[]);
        });
      });
    }else{
      axios.get('http://t2.xikang.com/apphybrid/api/v3/test/list', {
        params: {

        }
      })
        .then(function (response) {
          return callback(response.data.data||[]);
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  },
  getDetail:function (id,callback) {
    var item = {};
    if(isRpc){
      // alert("調用rpc：test_one："+id);
      this.ready(function() {
        var params = [{
          "id":id
        }]
        AlipayJSBridge.call('rpc', {
          operationType: 'test_one',
          requestData:params,
          headers:{}
        }, function (response) {
          return callback(response.data);
        });
      });
    }else{

      axios.get("http://t2.xikang.com/apphybrid/api/v3/test/one/"+id, {
        params: {
        }
      })
        .then(function (response) {
          return callback(response.data.data||[]);
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  } ,
  openApp:function (appId,pageName,param) {
    this.ready(function () {
      var aliParam = {
        showProgress:'NO'
      };
      if(pageName){
        aliParam = {
          url: '/index.html#/'+pageName+(param?'?params='+JSON.stringify(param):''),
          showProgress:'NO'
        };
      }
      AlipayJSBridge.call('startH5App', {
        appId: appId,
        param: aliParam
      }, function(result) {
        alert(JSON.stringify(result))
      });
    });
  }
}

