/* 路由配置全写这里 */
import Vue from 'vue'
import VueRouter from 'vue-router'

/* 开启debug模式 */
Vue.config.debug = true
Vue.use(VueRouter);

import Index from '../pages/index.vue'
import News from '../pages/news.vue'
import secondcomponent from '../pages/otherPages.vue'
import thirdcomponent from '../pages/otherPages2.vue'
import hellocomponent from '../pages/helloPage.vue'
import rpcListcomponent from '../pages/rpcListViewPage.vue'
import rpcDetailcomponent from '../pages/rpcDetailViewPage.vue'

export default new VueRouter({
  routes: [
    {path: '/', component: hellocomponent},
    {
      path: '/index',
      component: Index
    },
    {
      path: '/news',
      component: News, name: 'news', query: {
        params: ''
      }
    },
    {
      path: '/second',
      component: secondcomponent
    },
    {
      path: '/third',
      component: thirdcomponent
    },
    {
      path: '/hello',
      component: hellocomponent, name: 'hello'
    },
    {
      path: '/rpcList',
      component: rpcListcomponent, name: 'rpcList'
    },
    {
      path: '/rpcDetail',
      component: rpcDetailcomponent, name: 'rpcDetail',query: {
        item: {}
      }
    }
  ]
})



